<template>
  <div class="page" id="firmtopDetail">
    <div class="content">
      <div class="f18 p15 pt10 pb10 bgc-white">{{deliveryDetail.firm_name}}</div>
      <div class="mt10 p15 pt5 bgc-white">
        <div class="title">企业简介</div>
        <p v-html="deliveryDetail.firm_key"></p>
      </div>
      <div class="mt10 pl15 pr15 pt5 bgc-white">
        <h3 class="title">业务资源</h3>
        <div class="scrollWrap">
          <div class="box" :style="getScrollWidth()">
            <div class="tc p10" v-for="(res, index) in resource" :key="index">
              <div v-lazy:background-image="res.r_img.op_spic" class="res_img"></div>
              <div class="f12 gray mt5">{{ res.r_title }}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="mt15 p15 bgc-white">
        <div class="box">
          <div class="w120">联系方式：</div>
          <div class="bf1 gray">{{ deliveryDetail.phone }}</div>
          <a :href="'tel:' + deliveryDetail.phone"><img width="18" src="@/assets/images/industryUnion/phone.png"
                                                        alt=""></a>
        </div>
        <div class="box mt10">
          <div class="w120">公司地址：</div>
          <div class="bf1 gray">{{ deliveryDetail.f_address }}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'

  export default {
    name: 'firmtopDetail',
    data() {
      return {
        deliveryDetail: {},
        resource: []
      }
    },
    created() {
      this.pullList()
    },
    methods: {
      getScrollWidth() {
        return 'width:' + (this.resource && this.resource.length * 2) + 'rem;'
      },
      pullList() {
        let query = this.$route.query
        if (!utils.isEmpty(query)) {
          this.axios.get(this.apiConfig.getFirmDetail, {
            params: {id: query.id}
          }).then(res => {
            if (res.s === 1) {
              this.$store.dispatch('UPDATE_LOADINGSTATE', false)
              this.deliveryDetail = res.d
              this.resource = res.d.resource
            }
          })
        }

      }
    }
  }
</script>
<style lang="scss" scoped>
  @import '../../../assets/scss/Common/variables.scss';

  .w120 {
    width: rem(120px);
  }

  .title {
    font-size: rem(18px);
    font-weight: 600;
    padding-left: rem(15px);
    border-left: 3px solid $theme-danger;
    margin: rem(10px) 0;
  }

  .res_img {
    width: rem(120px);
    height: rem(120px);
    background: url(../../../assets/images/500.png) center no-repeat;
    background-size: 100%;
  }
</style>
